import React, { useRef, useEffect, useState, useCallback, useContext } from 'react'
import { renderToString } from 'react-dom/server'
import { SearchBar, Overlay } from '../components'
import { useCurrentLocation, fetchMask } from '../library'
import LocationContext from '../store/LocationContext'
import COLORS from '../assets/colors'
import IMAGES from '../assets/images'

const MapContainer = ({ loading, setLoading, mapRef }) => {
  const [mask, setMask] = useState([])

  let search = useRef('')
  let inputRef = useRef()
  const positions = useRef([])

  const { kakao } = window

  const { location, changeLocation } = useContext(LocationContext)

  const currentLocation = useCurrentLocation()

  useEffect(() => {
    currentLocation && changeLocation(currentLocation)
  }, [changeLocation, currentLocation])

  // ÏßÄÎèÑ ÎßàÏª§ ÏÉùÏÑ± Ìï®Ïàò
  const setMarker = useCallback(
    (latitude, longitude) => {
      //ÏßÄÎèÑÎ•º Îã¥ÏùÑ ÏòÅÏó≠Ïùò DOM Î†àÌçºÎü∞Ïä§
      const container = mapRef.current

      const options = {
        //ÏßÄÎèÑÎ•º ÏÉùÏÑ±Ìï† Îïå ÌïÑÏöîÌïú Í∏∞Î≥∏ ÏòµÏÖò
        center: new kakao.maps.LatLng(latitude, longitude), //ÏßÄÎèÑÏùò Ï§ëÏã¨Ï¢åÌëú.
        level: 3, //ÏßÄÎèÑÏùò Î†àÎ≤®(ÌôïÎåÄ, Ï∂ïÏÜå Ï†ïÎèÑ)
      }
      //ÏßÄÎèÑ ÏÉùÏÑ± Î∞è Í∞ùÏ≤¥ Î¶¨ÌÑ¥
      const map = new kakao.maps.Map(container, options)

      // ÏùºÎ∞ò ÏßÄÎèÑ/ Ïä§Ïπ¥Ïù¥Î∑∞ ÏßÄÎèÑ Ïª®Ìä∏Î°§
      const mapTypeControl = new kakao.maps.MapTypeControl()
      map.addControl(mapTypeControl, kakao.maps.ControlPosition.TOPRIGHT)

      // ÏßÄÎèÑ ÌôïÎåÄ Ï∂ïÏÜå Ï†úÏñ¥ Ïª®Ìä∏Î°§
      const zoomControl = new kakao.maps.ZoomControl()
      map.addControl(zoomControl, kakao.maps.ControlPosition.RIGHT)

      // ÎßàÏª§ ÏÑ§Ï†ï
      positions.current.forEach(position => {
        // ÎßàÏª§ Ïù¥ÎØ∏ÏßÄ Í¥ÄÎ†® Î≥ÄÏàò
        const imageSrc = IMAGES[position.remainStat],
          imageSize = new kakao.maps.Size(47, 40),
          imageOption = { offset: new kakao.maps.Point(27, 69) }

        const markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize, imageOption)

        const marker = new kakao.maps.Marker({
          map: map,
          position: position.latlng,
          image: markerImage,
        })

        const overlay = new kakao.maps.CustomOverlay({
          content: position.content,
          position: position.latlng,
          xAnchor: 0.7,
          yAnchor: 0.59,
        })

        kakao.maps.event.addListener(marker, 'mousedown', () => {
          overlay.setMap(map)
        })

        kakao.maps.event.addListener(marker, 'mouseup', () => {
          overlay.setMap(null)
        })
      })
    },
    [
      kakao.maps.ControlPosition.RIGHT,
      kakao.maps.ControlPosition.TOPRIGHT,
      kakao.maps.CustomOverlay,
      kakao.maps.LatLng,
      kakao.maps.Map,
      kakao.maps.MapTypeControl,
      kakao.maps.Marker,
      kakao.maps.MarkerImage,
      kakao.maps.Point,
      kakao.maps.Size,
      kakao.maps.ZoomControl,
      kakao.maps.event,
      mapRef,
    ],
  )

  // Enter ÏûÖÎ†•Ïãú Í≤ÄÏÉâ ÏàòÌñâ Ìï®Ïàò
  const onEnter = () => {
    if (window.event.keyCode === 13) {
      doSearch()
    }
  }

  // input Í∞íÏúºÎ°ú Í≤ÄÏÉâÏãú ÏÇ¨Ïö©ÌïòÎäî Ìï®Ïàò
  const doSearch = () => {
    const geocoder = new kakao.maps.services.Geocoder()

    const input = search.current.trim()
    if (input === '') {
      return alert('Ìïú Í∏ÄÏûê Ïù¥ÏÉÅ ÏûÖÎ†•Ìï¥ Ï£ºÏÑ∏Ïöî!')
    }

    geocoder.addressSearch(input, (result, status) => {
      if (status === kakao.maps.services.Status.OK) {
        const { Ga: latitude, Ha: longitude } = new kakao.maps.LatLng(result[0].x, result[0].y)
        const latlng = { latitude, longitude }
        changeLocation(latlng)
        inputRef.current.value = ''
        inputRef.current.placeholder = 'Í≤ÄÏÉâÏ§ë...üßê'
        getMask(latlng)
      } else {
        alert('Í≤ÄÏÉâ Í≤∞Í≥ºÍ∞Ä ÏóÜÏäµÎãàÎã§üò•')
        inputRef.current.value = ''
      }
    })
  }

  const getInputValue = e => {
    search.current = e.target.value
  }

  const getMask = useCallback(
    async ({ latitude, longitude }) => {
      setLoading(true)
      const response = await fetchMask({
        method: 'GET',
        url: `/storesByGeo/json?lat=${latitude}&lng=${longitude}&m=1000`,
      })

      // ÏïΩÍµ≠ Ï†ïÎ≥¥ Ï∂îÏ∂ú
      const {
        data: { stores },
      } = response

      if (stores) {
        setMask(mask => [...stores])

        positions.current = stores.map(store => {
          let remainStat = ''

          if (store.remain_stat) {
            remainStat = store.remain_stat
          } else {
            remainStat = 'noStat'
          }

          return {
            content: renderToString(
              <Overlay name={store.name} addr={store.addr} remainStat={store.remain_stat} />,
            ),
            latlng: new kakao.maps.LatLng(store.lat, store.lng),
            remainStat,
          }
        })

        setLoading(false)
        inputRef.current.placeholder = 'üö©Ï£ºÏÜåÎ•º ÏûÖÎ†•Ìï¥ Ï£ºÏÑ∏Ïöî.'
        setMarker(latitude, longitude)
      }
    },
    [kakao.maps.LatLng, setLoading, setMarker],
  )

  useEffect(() => {
    if (currentLocation === location) getMask(location)
  }, [currentLocation, getMask, location])

  return (
    <div>
      <SearchBar
        onEnter={onEnter}
        onClick={doSearch}
        onChange={getInputValue}
        placeholder="üö©Ï£ºÏÜåÎ•º ÏûÖÎ†•Ìï¥ Ï£ºÏÑ∏Ïöî."
        inputRef={inputRef}
      />
      <p
        style={{
          position: 'absolute',
          left: '50%',
          bottom: '2rem',
          transform: 'translateX(-50%)',
          zIndex: 1000,
          padding: '0.5rem',
          width: '80%',
          textAlign: 'center',
          borderRadius: '10rem',
          color: '#fff',
          background: COLORS.secondary,
          boxShadow: `0 1px 3px rgba(0, 0, 0, 0.12), 0 1px 2px rgba(0, 0, 0, 0.24)`,
        }}
      >
        1km ÎÇ¥Ïóê {mask && mask.length}Í∞úÏùò ÏïΩÍµ≠Ïù¥ ÏûàÏäµÎãàÎã§.
      </p>
    </div>
  )
}

export default MapContainer
