import { useState, useEffect } from 'react'

const getCurrentLocation = function(options) {
  const geo = navigator.geolocation

  if (!geo) {
    alert('‚ùå ÌòÑÏû¨ ÏúÑÏπòÎ•º Í∞ÄÏ†∏Ïò¨ Ïàò ÏóÜÏäµÎãàÎã§. Í≤ÄÏÉâÏ∞ΩÏóê ÏßÅÏ†ë ÏúÑÏπòÎ•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.')
    return new Promise(resolve => resolve({}))
  }

  return new Promise(resolve => geo.getCurrentPosition(resolve, alertError, geoOptions))
}

const useCurrentLocation = () => {
  const [location, setLocation] = useState({})
  useEffect(() => {
    const fetchCurrentLocation = async () => {
      try {
        const {
          coords: { latitude, longitude },
        } = await getCurrentLocation()
        setLocation({ latitude, longitude })
      } catch (error) {
        console.log(error)
      }
    }
    fetchCurrentLocation()
  }, [])

  return location
}

const geoOptions = {
  enableHighAccuracy: false,
  maximumAge: 0,
  timeout: Infinity,
}

const alertError = error => {
  let message = ''
  switch (error.code) {
    case error.PERMISSION_DENIED:
      message = '‚ùå ÌòÑÏû¨ ÏúÑÏπò ÏÇ¨Ïö©ÏùÑ Í±∞Ï†àÌñàÏäµÎãàÎã§.'
      break
    case error.POSITION_UNAVAILABLE:
      message = '‚ùå ÏúÑÏπò Ï†ïÎ≥¥Î•º ÏÇ¨Ïö©Ìï† Ïàò ÏóÜÏäµÎãàÎã§.'
      break
    case error.TIMEOUT:
      message = '‚ùå ÏöîÏ≤≠ ÏãúÍ∞ÑÏù¥ Ï¥àÍ≥ºÎêòÏóàÏäµÎãàÎã§.'
      break
    case error.UNKNOWN_ERROR:
      message = '‚ùå Ïïå Ïàò ÏóÜÎäî Ïò§Î•òÍ∞Ä Î∞úÏÉùÌïòÏòÄÏäµÎãàÎã§.'
      break
    default:
      message = '‚ùå ÌòÑÏû¨ ÏúÑÏπòÎ•º Í∞ÄÏ†∏Ïò¨ Ïàò ÏóÜÏäµÎãàÎã§.'
  }
  alert(message + 'Í≤ÄÏÉâÏ∞ΩÏóê ÏßÅÏ†ë ÏúÑÏπòÎ•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.')
}

export default useCurrentLocation
